body {
  background-image: url("/src/components/image/one.png");
  display: flex;
}
.Title {
  display: flex;
  width: 90%;
  padding: 10px;
  font-size: 20px;
  background: transparent;
  color: rgb(129, 255, 91);
  font-family: cursive;
  text-align: center;
  align-items: center;
  /* justify-content: center; */
}
.Title img {
  margin-right: 80px;
}

.Product-container {
  display: flex;
  margin: auto;
  width: 90%;
  margin-top: 20px;
  border: 4px solid #fafafa1c;
  box-shadow: 2px 2px 2px 0px rgba(207, 208, 207, 0);

  height: 80px;
  background-color: rgba(249, 249, 249, 0.344);

  backdrop-filter: blur(10px);


}

.product-download{
  text-decoration: none;
  color: black;
}
.Product-container img {
  width: 20%;
  border-radius: 10px;
  margin: 6px;
  margin-right: 20px;
}

.product-description {
  margin-top: 4px;
  display: table;
}

.product-description #product-title {
  font-size: 20px;
  font-family: sans-serif;
}

.product-description #product-details {
  color: saddlebrown;
}

.product-description #product-size {
  color: rgb(56, 182, 49);
}
