
.Navigation-Panel {   
    display: flex;
    width: 100%;
    height: 60px;
    background: #1dc2e7;
}
.Navigation-Panel > button{
    font-size: 20px;
    background: transparent;
    color: rgb(255, 255, 255);
    border: none;
    border-radius: 50%;
    width: 60px;
    height: 100%;
    transition: 0.2s;
}
.Navigation-Panel > button:hover{
    background-color:#15d0ff;
}


#search {
    display: flex;
    margin-top: auto;
    background: rgb(255, 255, 255);
    color: #ffffff;
    font-size: larger;
    text-align:left;
    border-radius: 0px 30px 30px 0px;
    outline: 2px solid rgb(59, 230, 24);
    border-style: none;
    margin-bottom: auto;
    width: 40%;
    height: 40px;
    transition: 0.1s;
}
#search::placeholder{
    padding-left: 10px;
    font-size: 18px;
    color: rgb(59, 230, 24);
}
#search:focus{
    outline: 2px solid rgb(37, 153, 241);
    background: #000000;
}


#search-btn{
    border-radius:30px 0px 0px 30px ;
    margin-top: auto;
    height: 45px;
    margin-bottom: auto;
    border: 10px solid rgb(59, 230, 24);
    background: rgb(59, 230, 24);
}